import React, { Component } from "react";
import CrossFade from "../components/Crossfade";

export default class Gallery extends Component {
    constructor(props) {
        super(props);
        this.data = props.data
    }

    state={
        'clicked': -1
    }

    exitImage() {
        if (this.state.clicked >= 0)  this.setState({clicked: -1})
    }

    render() {
        return <div style={{
            textAlign: 'center',
            lineHeight: 0,
            backgroundColor: 'rgb(30, 30, 30)',
            paddingTop: 'calc(10vh + 32px)',
            transformBox: 'content-box'
        }} onClick={(e) => {
                this.exitImage()
                e.stopPropagation()
            }}>
            {this.data.map((path, idx) => (<div onClick={() => this.setState({clicked: idx})}style={{
                display: 'inline-block',
                width: '45vw',
                margin: '10px',
                verticalAlign: 'top'
            }} key={'item' + idx}>
                <img src={path} style={{
                    animationDuration: '1s',
                    maxWidth: 'calc(45vw - 16px)',
                    maxHeight: 'calc(45vw - 16px)',
                    objectFit: 'contain',
                    verticalAlign: 'middle',
                    backgroundColor: 'white',
                    padding: '3px',
                }}/> </div>
            ))}

            <div style={{
                position: "fixed",
                top: '0', bottom: '0',
                left: '0', right: '0',
                margin: 'auto',
                pointerEvents: 'none',
                maxWidth: '90vw',
                maxHeight: '96vh',
            }} onKeyDown={(e) => console.log(e)} onClick={(e) => e.stopPropagation()}>
                <CrossFade img={this.state.clicked >= 0 ? this.data[this.state.clicked] : null} />
            </div>
            
        </div>
    }
}