import React, { Component } from "react";

export default class CrossFade extends Component {
    duration = 500

    state = {
        im1: null,
        im2: null,
        counter: false
    }

    static getDerivedStateFromProps({img}, state) {
        state[state.counter ? 'im1' : 'im2'] = img
        state.counter = !state.counter
        return state
    }

    constructor(props) {
        super(props)
    }
    
    render() {
        const shared_style = (offset) => ({
            objectFit: 'cover',
            overflow: 'hidden',
            maxWidth: 'calc(100% - 60px)',
            maxHeight: 'calc(100% - 60px)',
            marginRight: 'auto',
            marginLeft: 'auto',
            display: 'block',
            position: 'absolute',
            animationName: offset ? 'fadein' : 'fadeout',
            animationDuration: this.duration + 'ms',
            animationFillMode: 'forwards',
            left: 0,
            right: 0,
            border: '30px solid white',
            pointerEvents: offset ? 'all' : 'none'
        })

        return <div style={{ maxHeight: '100%', maxWidth: '100%' }}>
            {this.state.im1 ? <img src={this.state.im1} style={shared_style(!this.state.counter)}/> : null }
            {this.state.im2 ? <img src={this.state.im2} style={shared_style(this.state.counter)}/> : null }
        </div>
    }
}